<template>
  <div class="list">
    <myhead/>
    <div class="swiper-container">
      <div class="swiper-wrapper">
        
          <div class="swiper-slide" v-for="(item,index) in detailArr.img" :key="index">
            <viewer :images="detailArr.img" class="viewer">
              <img :src="item.src" alt="" preview preview-text="" class="imgSrc">
            </viewer>
          </div>
        
      </div>
      <div class="swiper-button-prev"></div>
      <div class="swiper-button-next"></div>
      <div class="swiper-pagination"></div>
    </div>
    <div class="connenct">
      <h3 class="name">{{detailArr.name}}</h3>
      <p  class="comment">:{{detailArr.comment}}</p>
      <span class="price">:{{detailArr.price}}</span>
      <span class="member">:{{detailArr.member}}</span>
      <p class="text"><span class="pinglun"></span>{{detailArr.text}}</p>
    </div>
    <div v-if="detailArr.phone" class="button">
      <a :href="'tel:'+detailArr.phone" class="phone">:{{detailArr.phone}}</a>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import mock from '@/mock'
  import Swiper from 'Swiper'
  import myhead from '@/components/myhead'
  export default {
    data(){
      return {
        detailArr:"",
        routerName:''
      }
    },
    components:{
      myhead
    },
    mounted(){
      this.init()
    },
    methods:{
      init(){
        localStorage.index = this.$route.params.index
        localStorage.name = this.$route.params.name
        let index = this.$route.params.index || localStorage.index
        let name = this.$route.params.name || localStorage.name
        let routerName = this.$route.params.routerName || localStorage.routerName
        axios.get('/api/navdetail').then(res=>{
          for(let key in res.data){
            if(key === localStorage.navid){
              for(let key1 in res.data[key]){
                if(key1 === routerName){
                  res.data[key][key1].forEach((item,i) => {
                    if(index == i){
                      this.detailArr = res.data[key][key1][index]
                    }
                  })
                }
              }
            }
          }
        })
        setTimeout(()=>{
          var mySwiper = new Swiper ('.swiper-container', {
            direction: 'horizontal', // 
            loop: true, // 
            autoplay:true,
            preventClicksPropagation:true,
            grabCursor:true
          })  
        },100)
      }
    }
  }
</script>

<style scoped>
.list{
  width:100%;
  height:100%;
  box-sizing: content-box;
  overflow-y: scroll;
}
.list .swiper-container{
  width: 100%;
  overflow: hidden;
}
.list .swiper-wrapper{
  width:700%;
  display: flex;
}
.list .swiper-wrapper .swiper-slide{
  width:37.5rem;
  height:20rem;
}
.list .swiper-wrapper .swiper-slide .viewer{
  width:100%;
  height:100%;
}
.list img{
  width:100%;
  height:100%;
}
.connenct{
  width: 100%;
  padding:0 1rem;
  box-sizing: border-box;
}
.name{
  font-size:1.8rem;
  padding-top:1rem;
}
.pinglun{
  font-size:1.4rem;
  color:rgb(139, 136, 139);
  display: block;
  padding-bottom: 1rem;
}
.text{
  font-size:1.4rem;
  padding:1rem 0;
  color:rgb(139, 136, 139);
}
.price{
  font-size:1.3rem;
  color:red;
  display: block;
  padding:1rem 0;
}
.member{
  font-size:1.3rem;
  color:blue;
  display: block;
}
.comment{
  font-size:1.5rem;
  color:chocolate;
  font-weight: bold;
  padding:1rem 0;
}
.button{
  width: 100%;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: skyblue;
}
.phone{
  font-size:1.5rem;
  color:#fff;
}
</style>


